  max-width: 300px;
  max-height: 750px;
  margin: auto;
  padding: .7em;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px #00000080;
}

#core {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

#core > div {
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  display: flex;
}

#core > div#game-over-page {
  justify-content: center;
  align-items: center;
}

#core > div#game-over-page > #game-over-message {
  position: absolute;
  bottom: 20px;
}

#core > div > .page-actions {
