button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 2px solid #2c3e50;
  border-radius: 3px;
  width: 100%;
  height: 40px;
  position: relative;
}

img#event-page-image {
  width: 100%;
}

img {
  border: 2px solid #2c3e50;
}

.photography-border {
  background-color: #fff;
  border-radius: 1px;
  width: 100%;
  height: 300px;
  padding: 10px 10px 30px;
}

.photography {
  filter: sepia();
  border: none;
}

button + button {
  margin-top: 10px;
}

button.loading:after {
  content: "";
  background-color: #ffffff1a;
  width: 100%;
  height: 100%;
  animation: 2s fill;
  position: absolute;
  top: 0;
  left: 0;
}

p {
  margin: 10px 0;
}

@keyframes fill {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

.line {
  text-decoration: line-through;
}

button:disabled {
  color: #2c3e50;
}

.green-color {
