}

.yellow-color {
  color: #ff0;
}

.green-color-border {
  color: #27ae60;
}

.red-color-border {
  color: #c0392b;
}

.yellow-color-border {
  color: #f1c40f;
}

.character-atributes-box {
  margin-top: 8px;
  padding-left: 10px;
  display: inline-block;
  position: absolute;
}

.character-content {
  height: 7.5em;
  position: relative;
